.slider__wrapper {
    position: relative;
    margin-bottom: 129px;
    left: -50px;
    opacity: 0;
    transition: all ease-in 0.5s;

    &.animate {
        left: 0;
        opacity: 1;
    }
}
@media(max-width: 768px) {
    .slider__wrapper { 
        margin-bottom: 100px;
    }
}
@media(max-width: 575px) {
    .slider__wrapper { 
        margin-bottom: 70px;
    }
}
.slider__items {
    position: relative;
    padding-left: 6px;
}
@media(max-width: 1024px) {
    .slider__items {
        padding-left: 11px;
    }
}
@media(max-width: 768px) {
    .slider__items {
        padding-left: 5%;
    }
}
@media(max-width: 721px) {
    .slider__items {
        padding-left: 1%;
    }
}
.slick-list {
    overflow: hidden;
    margin: 0 -10px;
}
.slick-track {
    display: flex;
}
.slider__arrows-wrapper {
    position: absolute;
    top: 50%;
    left: 0%;
    width: 100%;
}
@media(max-width: 768px) {
    .slider__arrows-wrapper {
        display: none;
    }
}
.slider__arrows-wrapper .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    font-size: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #FFFFFF;
    outline: none;

    
}
.slider__arrows-wrapper .slick-arrow.slick-prev { 
    left: 3%;
    background: rgba(255, 255, 255, 0.05) url('data:image/svg+xml;base64,')no-repeat 50% 50%;

}
.slider__arrows-wrapper .slick-arrow.slick-next { 
    //  right: 2.5%;
    left: 93%;
     background: rgba(255, 255, 255, 0.05) url('data:image/svg+xml;base64,')no-repeat 50% 50%;
    }
.slider__item {
    margin: 0 10px;
    outline: none;
}
@media(max-width: 575px) {
    .slider__item-img {
        margin: 0 auto;
    }
}
.slick-dots {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
.slick-dots li {
    display: inline-block;
    height: 3px;
    width: 50px;
    background: #D5D5D5;
}
.slick-dots li.slick-active {
    background: black;
}
.slick-dots li button {
    font-size: 0;
    border: none;
}