.apartment-design-slider__wrapper {
    position: relative;
    top: -50px;
    opacity: 0;
    transition: all 0.4s ease-in 0.4s;

    &.animate {
        top: 0;
        opacity: 1;
    }
}
.apartment-design-slider .slick-list {
    overflow: hidden;
    margin: 0 0 40px;
}
.slick-track {
    display: flex;
}
.apartment-design-slider__arrows-wrapper {
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    left: 0px;
    width: 100%;
}
@media(max-width: 768px) {
    .apartment-design-slider__arrows-wrapper {
        display: none;
    }
}
.apartment-design-slider__arrows-wrapper .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #FFFFFF;
    outline: none;
    font-size: 0;
}
.apartment-design-slider__arrows-wrapper .slick-arrow.slick-prev { 
    left: 3%;
    background: rgba(255, 255, 255, 0.05) url('data:image/svg+xml;base64,')no-repeat 50% 50%;

}
.apartment-design-slider__arrows-wrapper .slick-arrow.slick-next { 
    left: 93%;
     background: rgba(255, 255, 255, 0.05) url('data:image/svg+xml;base64,')no-repeat 50% 50%;
    }